import {css, StyleSheet} from "aphrodite";
import React, {Component} from "react";










export default class OptionBox extends Component {
  render() {
    const {options, title} = this.props;
    return (
      React.createElement('div', { className: css(styles.optionBox),}
        , React.createElement('span', { className: css(styles.title),}, title)
        , options.map(({text, checked, onToggle}) => (
          React.createElement('label', { key: text, className: css(styles.label),}
            , React.createElement('input', {
              className: css(styles.checkbox),
              type: "checkbox",
              checked: checked,
              onChange: onToggle,}
            )
            , text
          )
        ))
      )
    );
  }
}

const styles = StyleSheet.create({
  optionBox: {
    display: "inline-flex",
    flexWrap: "wrap",
    alignItems: "center",
    border: "1px solid white",
    borderRadius: 6,
    margin: 10,
    padding: 4,
    backgroundColor: "#333333",
  },
  title: {
    fontSize: "1.2em",
    padding: 6,
  },
  label: {
    display: "flex",
    alignItems: "center",
    marginLeft: 8,
    marginRight: 8,
  },
  checkbox: {
    marginRight: 4,
  },
});
