
import React, {Component} from "react";













export default class Editor extends Component {constructor(...args) { super(...args); Editor.prototype.__init.call(this);Editor.prototype.__init2.call(this);Editor.prototype.__init3.call(this); }
  __init() {this.editor = null}

  async componentDidMount() {
    setTimeout(this.invalidate, 0);
  }

  __init2() {this._editorDidMount = (monacoEditor, monaco) => {
    this.editor = monacoEditor;
    monaco.languages.typescript.typescriptDefaults.setDiagnosticsOptions({
      noSemanticValidation: true,
      noSyntaxValidation: true,
      noSuggestionDiagnostics: true,
    });
    this.invalidate();
  }}

  __init3() {this.invalidate = () => {
    if (this.editor) {
      this.editor.layout();
    }
  }}

  render() {
    const {
      MonacoEditor,
      code,
      onChange,
      isReadOnly,
      isPlaintext,
      options,
      width,
      height,
    } = this.props;
    return (
      React.createElement(MonacoEditor, {
        editorDidMount: this._editorDidMount,
        width: width,
        height: height,
        language: isPlaintext ? undefined : "typescript",
        theme: "vs-dark",
        value: code,
        onChange: onChange,
        options: {
          minimap: {enabled: false},
          readOnly: isReadOnly,
          ...options,
        },}
      )
    );
  }
}
